<template>
  <div id="userPhoneLogin">
    <div style="text-align:center">
      <mt-header fixed title="用户验证" style="background-color: #eaeaea; color:#000000; border:1px;">
      </mt-header>
    </div>
    <mt-field label="手机号码" placeholder="手机号码" v-model="userPhones" :disabled="isDefault()">
        <mt-button type="primary" @click="updataUserPhone" size="small">修改</mt-button>
    </mt-field>
    <hr style="background-color: #3366ff">
    <mt-field label="验证码" placeholder="验证码" readyonly v-model="codeMsg">
      <mt-button type="primary" @click="getImageMessage" size="small">{{codeTime}}</mt-button>
    </mt-field>
    <hr style="background-color: #3366ff">
    <br/>
    <div style="text-align:center">
      <mt-button type="primary" style="width: 80%" @click="checkedMessage">确 定</mt-button>
    </div>
  </div>
</template>
<script>
  export default {
    name: "userPhoneLogin",
    data() {
      return {
        userPhones: "",
        codeMsg: "",
        userCode: "",
        userTypeCode: "",
        userLoginCode: "",
        isTestIndex: true,
        codeTime: "获取验证码",
        userByCode:""
      }
    },
    methods: {
      getImageMessage() {
        if (this.userPhones) {
          this.$http.post("/dispatchMessage", {
            userCode: this.userCode,
            userPhones: this.userPhones
          }).then(
            (response) => {
              let type = response.data.status;
              console.log(type)
              if (type) {
                if (type == 'success') {
                  console.log('成功')
                  this.userByCode = response.data.userByCode
                } else {
                  alert(type)
                }
              }
            },
            (error) => {
              console.log(error);
            }
          )
        }

      },
      checkedMessage() {
        if(!this.userByCode){
          alert("请获取验证码")
          return;
        }
        if(this.codeMsg){
          this.$http.post("/checkedMessage", {
            userCode: this.userCode,
            userByCode: this.userByCode,
            codeMsg: this.codeMsg,
            userPhones: this.userPhones
          }).then(
            (response) => {
              let type = response.data.status;
              console.log(response.data.msg)
              if (type) {
                if (type == 'success') {
                  this.$router.push('/userIndex');
                } else {
                  alert(response.data.msg)
                }
              }
            },
            (error) => {
              console.log(error);
            }
          )
        } else {
          alert("请输入验证码")
        }
      },
      isDefault(field) {
        return this.isTestIndex;
      },
      updataUserPhone(){
          if (this.userPhones) {
            this.$router.push('/updataUserPhone');
          }
      }
    },
    created: function () {
      this.userPhones = this.$store.getters.user.userPhones;
      this.userCode = this.$store.getters.user.userCode;
      this.userTypeCode = this.$store.getters.user.userTypeCode;
      this.userLoginCode = this.$store.getters.user.userLoginCode;
    }
  }
</script>

<style scoped>

</style>



